<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="放款时效" name="first">
      <div class="p_6">
        <div class="search-wrapper">
          <!--<div class="search-content">-->
          <!--<el-date-picker-->
          <!--v-show="searchParam.category === 1"-->
          <!--v-model="searchParam.dateRange"-->
          <!--type="daterange"-->
          <!--placeholder="选择日期范围">-->
          <!--</el-date-picker>-->
          <!--</div>-->
          <div style="float: right;overflow: hidden;">
            <el-button slot="append" type="primary" icon="search" @click="search">重新查询</el-button>
          </div>
        </div>
        <chart :options="line" ref="line" theme="ovilia-green" auto-resize class="chartClass"></chart>

        <el-alert
          title
          type="info"
          description="提示：图表下方和右侧灰色区域两头可拖动控制相应联动区域的缩小和放大，方便数据查看，光标在图表区域内鼠标滑轮滑动亦可以！"
          show-icon
          :closable="closable"
        ></el-alert>
        <el-alert
          title
          description="警告：图表右上角工具条数据视图点开情况下，请勿点击查询按钮，如点击查询再点击数据视图内刷新后会与前一查询结果重合且图表类型变化，可点击工具条还原按钮还原！"
          show-icon
          type="warning"
          :closable="closable"
        ></el-alert>
      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<style scoped>
.el-select .el-input {
  width: 200px;
}

.pagination_position {
  padding-top: 20px;
}

.search-wrapper {
  margin: 0px auto 10px;
}

.chart-wrapper {
  display: -webkit-flex; /* Safari */
  display: flex;
  display: -ms-flex;

  /*flex主轴方向和主轴溢出排版方式的简写*/
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
  -ms-flex-flow: row wrap;

  /*内容在主轴上的对齐方式*/
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -ms-justify-content: space-between;

  /*内容项目在交叉轴上的对齐方式*/
  -webkit-align-items: stretch;
  align-items: stretch;
  -ms-align-items: stretch;

  /*多轴的时候轴线的对齐方式*/
  -webkit-align-content: stretch;
  align-content: stretch;
  -ms-align-content: stretch;
}

.echarts {
  width: 100%;
}

.right {
  float: right;
}
.warning {
  color: #f7ba2a;
}
</style>

<script>
import Common from "../../../utils/common";
import auth from "../../../utils/authenticate";
import { Notification } from "element-ui";

import "echarts/lib/component/grid";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";

// built-in theme
import "echarts/theme/dark";
import initChartData from "../../../data/initChart";

import $ from "jquery";
import deepcopy from "deepcopy";

export default {
  props: [],
  components: {},
  data() {
    let date = new Date();
    let now = new Date();

    return {
      closable: false,
      line: deepcopy(initChartData),
      hashes: [1],
      searchParam: {},
      activeName: "first",
      isFullScreen: false,
      clearable: false,
      lineGroup: {},
    };
  },
  computed: {},
  methods: {
    formatDate(date) {
      if (typeof date == "number" || typeof date == "string") {
        date = new Date(date);
      }
      var _year = date.getFullYear();
      var _month = date.getMonth();
      var _day = date.getDate();
      var _hour = date.getHours();
      _day = _day < 10 ? "0" + _day : _day;
      _month = _month < 9 ? "0" + (_month + 1) : _month + 1;
      return `${_year}/${_month}/${_day} ${_hour}`;
    },

    clearLine() {
      this.line.legend.data = [];
      this.line.xAxis && (this.line.xAxis[0].data = []);
      this.line.series = [];
      this.showPercent = false;
      this.line.yAxis[0].axisLabel = {
        formatter: "{value}",
      };
      //格式化tooltip的坐标轴指示器显示数值
      this.line.tooltip.formatter = null;
      this.line.tooltip.axisPointer.label = { formatter: "{value}" };
    },
    setDataView(categorys, xAxisData) {
      var thead = "",
        trTd = [],
        tbodyTr = "";
      for (var i = 0; i < categorys.length; i++) {
        thead += "<td>" + this.line.series[i].name + "</td>"; //头部
      }

      for (var j = 0, l = xAxisData.length; j < l; j++) {
        trTd[j] = "";
        for (var i = 0; i < categorys.length; i++) {
          trTd[j] += "<td>" + this.line.series[i].data[j] + "</td>";
        }

        tbodyTr += "<tr>" + "<td>" + xAxisData[j] + "</td>" + trTd[j] + "</tr>";
      }
      this.line.toolbox.feature.dataView.optionToContent = function() {
        var table =
          '<table style="width:100%;text-align:center"><tbody><tr>' +
          "<td>时间</td>" +
          thead +
          "</tr>" +
          tbodyTr +
          "</tbody></table>";
        return table;
      };
    },
    /**
     * 配置chart图表数据
     * @param categorys
     * @param xAxisData
     */
    setOptions(categorys, xAxisData) {
      this.line.dataZoom[0].start = 67;
      let len = xAxisData.length;
      let colors = [
        "rgba(255,144,128,1)",
        "rgba(0,191,183,1)",
        "rgba(252,230,48,1)",
      ];
      this.line.xAxis[0].data = xAxisData.map(item => {
        return this.formatDate(item);
      });
      this.line.legend.data = categorys.map(item => {
        return item.value;
      });

      for (var i = 0; i < categorys.length; i++) {
        this.lineGroup[categorys[i].value] = i;
        let seriesOptions = {
          name: categorys[i].value,
          type: "line",
          label: {
            normal: {
              show: true,
            },
          },
          barMaxWidth: 30,
          barGap: "80%",
          symbolSize: 5,
          symbol: "circle",
          areaStyle: { normal: {} },
          data: Array(len).fill(0),
          itemStyle: {
            normal: {
              color: colors[i],
              label: {
                show: true,
                position: "insideTop",
                formatter(p) {
                  return p.value > 0 ? p.value : "";
                },
              },
            },
          },
        };
        if (categorys[i].key == "avgVerFk") {
          seriesOptions.itemStyle.normal.label.position = "top";
        }
        this.line.series.push(seriesOptions);
      }
    },

    search() {
      let queryData = {};

      this.$refs.line.showLoading({
        text: "正在加载",
        color: "#4ea397",
        maskColor: "rgba(255, 255, 255, 0.4)",
      });

      Common.ajax({
        url: "/web/v1/audit/getThreeDay",
        type: "post",
        contentType: "application/json",
        data: queryData,
        success: response => {
          this.$refs.line.hideLoading();
          let resData;
          this.clearLine();
          if (response.data && response.data.list) {
            resData = response.data;
            if (!response.status) {
              this.setOptions(resData.categories, resData.dates);
              resData.list.forEach((item, index, arr) => {
                let dataIdx = this.line.xAxis[0].data.indexOf(
                  this.formatDate(item.startTime * 1000),
                );
                if (dataIdx >= 0) {
                  this.line.series[this.lineGroup["进件到提交"]].data[dataIdx] =
                    Math.ceil(item.avgJjSub / 60) +
                    "." +
                    (Math.abs(item.avgJjSub) % 60);
                  this.line.series[this.lineGroup["提交到审核"]].data[dataIdx] =
                    Math.ceil(item.avgSubVer / 60) +
                    "." +
                    (Math.abs(item.avgSubVer) % 60);
                  this.line.series[this.lineGroup["审核到放款"]].data[dataIdx] =
                    Math.ceil(item.avgVerFk / 60) +
                    "." +
                    (Math.abs(item.avgVerFk) % 60);
                }
                //                                    else {
                //                                        this.line.series[this.lineGroup["进件到提交"]].data[dataIdx] = 0;
                //                                        this.line.series[this.lineGroup["提交到审核"]].data[dataIdx] = 0;
                //                                        this.line.series[this.lineGroup["审核到放款"]].data[dataIdx] = 0;
                //                                    }
              });
              this.setDataView(resData.categories, resData.dates);
            }
          }
        },
        error: err => {
          this.$refs.line.hideLoading();
          let error = err.message || err;
          Notification.error({
            title: "错误",
            message: error,
          });
        },
      });
    },
    handleClick(tab, event) {
      let index = tab.index;
      if (index == 0) {
        this.activeName = "first";
      }
    },
  },
  created() {},
  mounted() {
    let cvHeight = $(".contents").height() - 200;
    $(".chartClass").css("height", cvHeight);
    //调用echarts实例resize函数
    this.$refs.line.resize();

    this.search();
  },
  beforeDestroy() {
    this.clearLine();
  },
  watch: {},
};
</script>
